* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
  border-radius: 8px;
  border: 1px solid grey;
  
}

.card-grid {
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;  /* para que los bordes no se salgan del container */
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

